import React from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';

// Components
import BigTitle from '../components/BigTitle/BigTitle';
import Layout from '../components/Layout/Layout';
import More from '../components/More/More';

// Images
import insta from '../img/insta.png';
import whatsapp from '../img/whatsapp.png';
import telegram from '../img/telegram.png';
import gmail from '../img/gmail.png';
import callmain from '../img/callmain.png';
import address from '../img/address.png';
export const query = graphql`
	{
		file(relativePath: { eq: "img/contact.png" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
	}
`;
function ContactPage({ data }) {
	return (
		<Layout title="تماس با ما">
			<div className="container">
				<div className="row">
					<div className="col-lg-6 col-md-6 col-sm-12 col-12">
						<Img fluid={data.file.childImageSharp.fluid} alt="contact" style={{ width: '250px' }} />
					</div>
					<div className="col-lg-6 col-md-6 col-sm-12 col-12">
						<BigTitle>مــیـزبـان صـدای گـرمـتـان هـسـتـیـم</BigTitle>
					</div>
				</div>
				<div className="pt-4 pb-4">
					<p dir="rtl" className="text-right">
						در هر ساعت از شبانه روز میتوانید با شماره های زیر تماس بگیرید و با ما در ارتباط باشید، گزینه های
						زیر پل ارتباطی بین ما و شماست، حتی روزهای تعطیل و یا میتوانید از طریق ایمیل پیغامی برای ما ارسال
						کنید،همکاران ما با کمال میل در اولین فرصت پاسخگوی شما خواهند بود.
					</p>
				</div>
				<div className="row">
					<div className="col-lg-6 col-md-6 col-sm-12 col-12 pt-2 pb-2">
						<div dir="rtl" className="d-flex align-items-center">
							<img style={{ width: '40px' }} src={insta} alt="insta" className="img-fluid" />
							<p className="text-right m-0 mr-2" dir="rtl">
								آدرس اینستاگرام : irankeshtnovin@
							</p>
						</div>
					</div>
					<div className="col-lg-6 col-md-6 col-sm-12 col-12 pt-2 pb-2">
						<div dir="rtl" className="d-flex align-items-center">
							<img style={{ width: '40px' }} src={whatsapp} alt="wp" className="img-fluid" />
							<p className="text-right m-0 mr-2" dir="rtl">
								شماره واتساپ : ۰۹۰۴۶۵۵۸۲۸۳
							</p>
						</div>
					</div>
				</div>
				<div className="row">
					<div className="col-lg-6 col-md-6 col-sm-12 col-12 pt-2 pb-2">
						<div dir="rtl" className="d-flex align-items-center">
							<img style={{ width: '40px' }} src={callmain} alt="telegram" className="img-fluid" />
							<p className="text-right m-0 mr-2" dir="rtl">
								شماره تماس مشاوره و پشتیبانی : ۰۹۰۴۶۵۵۸۲۸۳
							</p>
						</div>
					</div>
					<div className="col-lg-6 col-md-6 col-sm-12 col-12 pt-2 pb-2">
						<div dir="rtl" className="d-flex align-items-center">
							<img style={{ width: '40px' }} src={telegram} alt="telegram" className="img-fluid" />
							<p className="text-right m-0 mr-2" dir="rtl">
								تلگرام : ۰۹۰۴۶۵۵۸۲۸۳
							</p>
						</div>
					</div>
				</div>
				<div className="row">
					<div className="col-lg-6 col-md-6 col-sm-12 col-12 pt-2 pb-2">
						<div dir="rtl" className="d-flex align-items-center">
							<img style={{ width: '40px' }} src={gmail} alt="gmail" className="img-fluid" />
							<p className="text-right m-0 mr-2" dir="rtl">
								آدرس ایمیل : irankeshtnovin@gmail.com
							</p>
						</div>
					</div>
					<div className="col-lg-6 col-md-6 col-sm-12 col-12 pt-2 pb-2">
						<div dir="rtl" className="d-flex align-items-center">
							<img style={{ width: '40px' }} src={address} alt="insta" className="img-fluid" />
							<p className="text-right m-0 mr-2" dir="rtl">
								آدرس : جاده سلماس روستای بالو جاده قزل عاشق
							</p>
						</div>
					</div>
				</div>
				<div className="pt-5 pb-5">
					<More />
				</div>
			</div>
		</Layout>
	);
}

export default ContactPage;
